import { Mark, Data } from '@ali/4ever-cangjie';

export interface VertAlignData extends Data {
  value: 'superscript' | 'subscript' | 'baseline';
}

interface VertAlign extends Mark<VertAlignData> {}

const VertAlign = {
  TYPE: 'vertAlign',

  create(vertAlign: VertAlignData['value']): VertAlign {
    return Mark.create<VertAlignData>({
      type: VertAlign.TYPE,
      data: { value: vertAlign },
    });
  },

  isVertAlign(maybeVertAlign: any): maybeVertAlign is VertAlign {
    return Mark.isMark(maybeVertAlign) && maybeVertAlign.type === VertAlign.TYPE;
  },
};

export default VertAlign;
